import { put, call, takeEvery } from "redux-saga/effects";

import { FETCH_USERS, setUsers } from "../store/userReducer";
import { usersQuery } from "../requests/getUsers";

const fetchUsers = () =>
  fetch("https://graphqlzero.almansi.me/api", {
    method: "POST",
    headers: { "content-type": "application/json" },
    body: usersQuery, //  ,    graphql query
  });

function* fetchUserWorker() {
  const response = yield call(fetchUsers); //   (response)
  const parsedJson = yield call(
    () => new Promise((res) => res(response.json())) //    
  );
  const usersListOnly = parsedJson.data.users.data; //     
  usersListOnly.forEach((item) => {
    item.id = Number(item.id);
  });
  yield put(setUsers(usersListOnly)); //   
}

export function* userWatcher() {
  yield takeEvery(FETCH_USERS, fetchUserWorker);
}
